.about-content {
    display: flex;
    margin: 10vh;
    /* width: 100%; */
    height: 100%;
}

.about-my-photo {
    margin-right: auto;  /* position at the left by setting margin-right auto */
    width: 100%;
    padding: 10px 75px 10px 50px;
}

.about-my-photo img {
    max-width: 25vw;
    max-height: 70vh;
    border-radius: 10%;
    box-shadow: var(--darkGrayColor) 25px 25px 50px 15px;
}

.about-text {
    margin: 0vh 40vh 5vh auto;  /* position at the right by setting margin-left auto */
    width: 100%;
    font-size: 3.5vh;
}

.i-am-list {
    margin: 0;
}